<template>
<nav-bar>
  <span slot="title" class="title">广告管理 - {{advertisement.title}}</span>
</nav-bar>
<table class="table">
  <tr>
    <td style="width:200px;padding-left:50px;">
      <img @click="inputFileClick" alt="广告图标" class="img-circle" width="100" height="100" v-bind:src="advertisement.avatar" />
      <input @change="uploadAvatar" v-el:avatar class="avatar" type="file" accept="image/*" style="display:none;" />
    </td>
    <td>
      <div class="content-form">
        <form @submit="update">
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">广告标题</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="advertisement.title" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">广告内容</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="advertisement.content" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">广告分成</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="advertisement.divide_into" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
            <div class="col-sm-10">
              <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
            </div>
          </div>
        </form>
      </div>
    </td>
  </tr>
</table>
<table class="table">
  <tr>
    <td width="150">
      <p>
        活动二维码
      </p>
    </td>
    <td width="100">
      <div id="qrcode"></div>
    </td>
    <td>
      <p>
        只要用你的微信扫一扫，就能打开你的活动页面。
        然后分享到你的朋友圈，就能让关注你的顾客在活动页面里更好地参加你的活动。
      </p>
    </td>
  </tr>
</table>
<table class="table">
  <tr>
    <td colspan="4">
      参加活动的用户
    </td>
  </tr>
  <tr v-for="shop_advertisement_user in advertisement.shop_advertisement_users">
    <td>
      {{shop_advertisement_user.user.nickname}}
    </td>
    <td>
      已转发 {{shop_advertisement_user.forwarding_times}} 次
    </td>
    <td>
      每次分成 {{advertisement.divide_into}} 元
    </td>
    <td>
      总分成 {{advertisement.divide_into * shop_advertisement_user.forwarding_times}} 元
    </td>
  </tr>
</table>
</template>
<script>
  export default {
    data () {
      return {
        advertisement: {}
      }
    },
    route: {
      data (transition) {
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/shop_advertisements/one.json', { access_token: localStorage.token, shop_advertisement_id: params.advertisement_id }).then(function (response) {
          transition.next({
            advertisement: response.data
          })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    methods: {
      inputFileClick (e) {
        return this.$els.avatar.click()
      },
      uploadAvatar (e) {
        e.preventDefault()

        if (this.$els.avatar.value === '') return
        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('shop_advertisement_id', this.$route.params.advertisement_id)
        formData.append('shop_advertisement[avatar]', this.$els.avatar.files[0])

        this.$http.put('http://jiancan.me/api/m1/shop_advertisements/one.json', formData).then(function (response) {
          this.$set('advertisement', response.data.shop_advertisement)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      },
      update (e) {
        e.preventDefault()

        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('shop_advertisement_id', this.$route.params.advertisement_id)
        formData.append('shop_advertisement[title]', this.advertisement.title)
        formData.append('shop_advertisement[content]', this.advertisement.content)
        formData.append('shop_advertisement[divide_into]', this.advertisement.divide_into)

        this.$http.put('http://jiancan.me/api/m1/shop_advertisements/one.json', formData).then(function (response) {
          this.$set('advertisement', response.data.shop_advertisement)
          this.$route.router.go({ path: '/advertisements' })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    },
    ready () {
      jQuery('#qrcode').qrcode({width: 80, height: 80, text: 'http://jiancan.me/weixin/advertisement?advertisement_id=' + this.$route.params.advertisement_id})
    }
  }
</script>



